import React from 'react';
import { InsertLink, Close } from '@material-ui/icons'

const Card = ({ card, remove }) => {
  const removeClicked = (e) => {
    e.stopPropagation();
    remove();
  }

  return (
    <div className="card-container">
      <div className='product-img' >
        <img src={card.photo} alt={card.name} className='mb-3'
          style={{ objectFit: 'contain', height: '100%', width: '100%', display: 'flex', marginLeft: 'auto', marginRight: 'auto' }}
        />
      </div>
      <div className='d-flex justify-content-end align-items-center'>
        <div className='item-name'>{card.name}</div>
        <div className='insert-link'><a onClick={e=>e.stopPropagation()} href={card.url || card.photo} rel="noopener noreferrer" target="_blank"><InsertLink /></a></div>
      </div>
      {remove && <a onClick={removeClicked} className="link right" >
        <div>
          <Close />
        </div>
      </a>}
    </div>
  );
};

export default Card;
